import dealWithTime from '../src/util/dealWithTime.js'
export default {
  name:'weekly-status',
  computed:{
  },
  props:['status'],
  created() {
    // 获取剩余时间
    this.getLeftTime();
  },
  data() {
    return {
      leftTime: {
        left_days: 0,
        left_hours: 0,
        left_minutes: 0,
        left_seconds: 0,
      },
    };
  },
  methods:{
    getLeftTime() {
      this.leftTime = dealWithTime();
      this.$nextTick();
    }
  }
}
